html{scroll-behavior:smooth}body P{margin:0}body .home-container{font-family:'Josefin Sans', sans-serif}body .home-container .header-container{background:url(/wedding/static/media/flower-1.70d9d1cb.png) left bottom/20vw no-repeat,url(/wedding/static/media/flower-2.03622fb5.png) right bottom/20vw no-repeat,url(/wedding/static/media/flower-3.9c92d46b.png) left top/20vw no-repeat,url(/wedding/static/media/flower-4.9b31dc3b.png) right top/20vw no-repeat;background-color:#211f2f;height:100vh;width:100%}body .home-container .header-container .info{display:-webkit-flex;display:flex;color:white;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;text-align:center;height:85%}body .home-container .header-container .info-undangan img{width:14vmin}body .home-container .header-container .info-undangan h1{font-family:'Pacifico', cursive;font-size:8vmin;line-height:200%;margin:0;font-weight:300}body .home-container .header-container .info-undangan p{color:#FFC700;font-size:3vmin;margin:0;line-height:200%;font-weight:400}body .home-container .header-container .info-tamu{margin-top:3%;margin-bottom:3vmin;width:100%}body .home-container .header-container .info-tamu p{margin:0}body .home-container .header-container .info-tamu p:first-child{font-family:'Pacifico', cursive;font-size:3vmin}body .home-container .header-container .info-tamu p:nth-child(2){font-size:4.2vmin;margin:1vmin 0 2.5vmin}body .home-container .header-container .info-tamu div{width:60vmin;height:.25vw;margin:0 auto;background:white}body .home-container .header-container .info .btn-more{display:-webkit-inline-flex;display:inline-flex}body .home-container .header-container .info .btn-more .btn-link{text-decoration:none;background-color:white;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;padding:.5vmax 5vmin .5vmax 5vmin;border-radius:2vmin;margin-left:1vw;margin-right:1vw;margin-top:1vw;margin-bottom:1vw}body .home-container .header-container .info .btn-more .btn-link p{color:#211f2f;font-size:3vmin}@media screen and (max-width: 800px){body .home-container .header-container .info .btn-more{-webkit-flex-direction:column;flex-direction:column}}body .home-container .header-container .info .hide{opacity:0}body .home-container .header-container .info .show{transition:opacity 0.5s;opacity:1}body .home-container .header-container .bottom-ornament{width:100%;height:100%;z-index:1;bottom:0;background:url(/wedding/static/media/flower-1.70d9d1cb.png) left bottom/20vw no-repeat,url(/wedding/static/media/flower-2.03622fb5.png) right bottom/20vw no-repeat}body .home-container .btn-audio{position:fixed;z-index:10;bottom:4vmin;left:4vmin}body .home-container .btn-audio img{width:12vmin;cursor:pointer}

